header {
	width: 100%;
	position: fixed;
	height: 79px;
	margin: 0 auto;
	z-index: 1000;
	background-blend-mode: overlay, ;
	background-image: linear-gradient(to top, #232425, #ffffff), linear-gradient(to bottom, #262626, #262626);
	nav	{
	display: flex;
	width: 100%;
	height: 100%;
	position: relative;
	align-items: center;
	padding-right: 30px;
	.logo	{
		position: fixed;
		width: 212px;
		height: 17px;
		left:32px;
		overflow: hidden;	
	}
	.burger {
		position: fixed;
		width: 40px;
		right:  40px;
		display: none;

	}
	ul {
		position: fixed;
		top: 0;
		right: 0;
		height: 100%;
		display: flex;
		list-style: none;

		
		li {
			height: 79px;
			min-width: 85px	;
			display: inline-flex;
			position: relative;
			border-left : 1px solid  hsla(0, 3%, 37%, 0.438);
			a.m {
				@include flexCenter(row);
				cursor: pointer;
				font-size: 20px;
				color: #7a7575;
				font-family: montserrat;
				span {
					font-size: 17px;
				}
				&:hover {
					background: #000;
					color: white;
				}
			}
			
		}
	
		
	}
}
}

ul.droop 	{
	position: absolute;
	top: 79px;
	right:-50px;
	height: 180px;
	display: none;
	width: 185px;
	// border: .1px solid rgb(94, 93, 93);
	border-top: 1px solid rgb(207, 205, 205);

	box-shadow: 4px 4.5px 0 0 rgba(0, 0, 0, 0.18);
	transition: .5s;
	


	li {
		height: 45px;
		width: 100%;
		border-top: 1px solid rgb(207, 205, 205);
		background: #ffffff;
	

		
		
		a {
		@include flexCenter(row);
		
		color: #000;
			div {
			justify-content: space-between;
			//  border-top: .2px solid rgb(207, 205, 205);
			border-right: .2px solid rgb(207, 205, 205);

			 
				
				padding:10px;
			}
			.t {
				width: 80%;
			}
			
		}
	} 
}

li.share:hover ul.droop {
	display: block;
}
