@media (min-width: 768px ) and (max-width: 979px){

}
@media (max-width: 767px ) {
	header {
		nav {
			ul{
				display: none;
			}
			.burger {
				
				display: block;
				z-index: 1010;				
		  }

		}
	}
	

.container1 {
	.sidebar {

		left: -70px;
		transition: .5s;
		
	}
	#masonry {
		margin-left: 0;
		.item {
			width: 49.5%;
			.news {
				margin: 0;
				padding: 0;
			}
		}
	}
	

}
.HeaderActive {
	position: fixed;
	left: 0!important;
	transition: .5s;

}

}
@media (max-width: 480px) {
	.container1 {
		#masonry {
			display: block;
			.item {
				width: 100%;
				padding: 0;
			}
	}

}
}