.pic {
	width:100%;
	position: relative;
	// min-height: 300px;
	overflow: hidden;
	display: flex;
	 align-items: center;
	 justify-content: center;

	
	img {

		width: 100%;
		display: block;

	}
}
.news#one {
	border-bottom: 5px solid  #2eb398!important;
}
.news#two {
	border-bottom: 5px solid #e76049!important;
}
.news#three {
	border-bottom: 5px solid darkblue!important;
}

.news#five {
	border-bottom: 5px solid #90899c!important;
}
.news#six {
	border-bottom: 5px solid  #14b9d6!important;
}
.news .pic .hover { 
	 position: absolute;
	 
	 left: 0;
	 right: 0;
	 top: 0;
	//  z-index: 333;
	 width: 100%;
	 height: 100%;
	background: rgba(255, 68, 0, 0);
	transition: .5s;
	display: flex;
	align-items: center;;
	

	
	a {
		display: block;
		width: 300px ;
		opacity: 0;
		// z-index: 333;
		margin: 0 auto;
	transition: .5s;
	}
}
.news .pic:hover .hover a {
	opacity: 1;
}
.news .pic:hover .hover {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	z-index: 555;
	width: 100%;
	height: 100%;
	display: flex;
  background: #e76049;
  opacity: 0.9;
}
