@mixin flexCenter($direction) {
	display: flex;
	width: 100%;
	height: 100%;
	align-items: center;
	justify-content: center;
	flex-direction: ($direction);
	
}
@mixin iconBox($color) {
	a {
		display: flex;
		position: relative;
		width: 69px;
		height: 69px;
		align-items: center;
		font-size:17px ;
		border-bottom: 2px solid #e7e7e7;
		color : $color;
		transition: .2s;
		z-index: 1000;
		i {
			position: absolute;
			left: 25px;
		}
		span {
			margin-left: 80px;
			padding-right: 30px;
			color: white;
			font-size: 14px;
			display: none;
			
			
		}
		&:hover {
			color:white ;
			background-color: $color;
			border-bottom: .2px solid $color;
			box-shadow: 0 0 3px grey;
			
			z-index: 1000;
			width: 170px;
			
			span {
				display: block;
			}
			
		}
		
	}
	
}
